/* eslint-disable max-len */
import React, { InputHTMLAttributes } from "react";

import { IconName } from "../Icon/Icon";
import { Margins } from "../styleguide";
import { TooltipPosition } from "../Tooltip";

type InputAttributes = InputHTMLAttributes<HTMLInputElement>;

export type InputValidation = "valid" | "invalid";

export interface InputProps extends StyledInputProps, StyledRequirementProps, Omit<InputBase, "onChange"> {
	dataQA: string
	"data-1p-ignore"?: string
	label?: string
	id: string
	requirements?: string[]
	rightTooltipPosition?: TooltipPosition
	topButtonLinkText?: React.ReactNode
	topButtonLinkAction?: () => void
	trailingButtonLeftIcon?: IconName
	trailingButtonText?: string
	trailingButtonAction?: () => void
	/**
	 * Enables tags in the Input. To create the tag, press "Enter" or "Comma" when
	 * typing text in the Input. Tags data will be return in the onChange as:
	 * @example onChange={({tags}) => setTags(tags)}
	 */
	isTagsInput?: boolean
	onChange?: ((data: { event?: React.ChangeEvent<HTMLInputElement>, tags?: string[] }) => void)
	value?: string | number | string[]
}

export interface StyledInputProps extends Margins, Pick<InputBase, "disabled"> {
	prefix?: string
	trailingButtonWidth?: number
	suffix?: string
	validation?: InputValidation
	rightIcon?: IconName
	leftIcon?: IconName
	rightTooltipText?: string
}

export type StyledRequirementProps = Pick<StyledInputProps, "validation">;

export interface InputBase extends InputAttributes {
	dataQA: string
}
